.repo-container {
    margin: 50px 50px;
    padding-top: 20px;
    border-top: 1px #00000033 solid;
  }
  
  .repo-container .repo-meta {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  }
  
  .repo-container .repo-meta div {
    display: inline-block;
    margin: 13px;
    margin-left: 0;
  }
  
  .repo-container h2 a {
    color: #0366d6;
    text-decoration: none;
  }