.dummy-tabs {
    margin: 0 50px;
  }
  
  .dummy-tabs ul {
    height: 50px;
    border-bottom: 1px silver solid;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    padding-left: 0;
  }
  
  .dummy-tabs ul li {
    display: inline-block;
  }
  
  .dummy-tabs ul li a {
    font-weight: 300;
    padding: 16px 8px;
    margin-right: 16px;
    text-decoration: none;
    color: black;
  }
  
  .dummy-tabs ul li a code {
    background: rgba(27, 31, 35, 0.08);
    opacity: 0.7;
    border-radius: 50%;
  }
  
  .dummy-tabs ul li:nth-of-type(2) {
    border-bottom: 2px red solid;
  }
  
  .dummy-tabs ul li:nth-of-type(2) a {
    font-weight: 600;
  }
  
  #filter-repo {
