#profile {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin: 0 150px;
  }
  
  #profile .search-repo {
    width: 75%;
  }
  
  @media (max-width: 1050px) {
    #profile {
      margin: 0 75px;
    }
  }
  
  @media (max-width: 900px) {
    #profile {
      margin: 0 20px;
    }
    #profile .search-repo #filter-repo {
      width: 80%;
    }
  }