#profile-search {
    text-align: center;
  }
  
  #profile-search label {
    margin-right: 30px;
  }
  
  #profile-search label input {
    width: 200px;
  }
  
  #profile-search button {
    background: black;
    border: 0;
    color: white;
  }
  
  #profile-search button:focus {
    outline: none;
  }
  
  #profile-search button:hover {
    background: grey !important;
  }
  
  .btn {
    font-size: 17px;
    height: 35px;
    width: 75px;
    border-radius: 5px;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    cursor: pointer;
  }
  
  .input-field {
    font-size: 17px;
    height: 35px;
    border-radius: 5px;
    border: 1px silver solid;
    padding-left: 10px;
  }
  
  .input-field:focus {
    border-color: #2188ff;
    outline: none;
    -webkit-box-shadow: 0px 0px 35px -7px #2188ff;
    box-shadow: 0px 0px 35px -7px #2188ff;
  }